import React, { useState } from 'react';
import { random } from 'lodash';
import './App.css';

const cycle = ['number', 'wait', 'add'];
const periodInMs = 2000;

export const App = () => {
  const [iteration, setIteration] = useState(0);
  const nextIteration = (iteration + 1) % cycle.length;
  const state = cycle[iteration];

  setTimeout(() => setIteration(nextIteration), periodInMs);

  return <h1>{ getValue(state)}</h1>;
};

const getValue = state => {
  switch (state) {
    case 'number': return digitLine(4);
    case 'wait': return '';
    case 'add': return `Add ${random(1, 4)}`;
  }
};

const randomDigit = () => random(1, 9);
const digitLine = length => {
  const digits = [];
  while (digits.length < length) {
    const digit = randomDigit();
    if (!digits.includes(digit)) digits.push(digit);
  }

  return digits.join('');
};
